import React, { Component } from 'react';

class Video extends Component {
  render() {
    return (
      <section className="section about">
        <div className="container container-xl about__grid">
          <div className="about__column">
            <h2 className="title">Conhea o Corthy</h2>
            <div className="description">
              <p>Video curto exemplicando nossa proposta para voc</p>
              <br />
              Corthy na frente do seus negcios onde for.
              <ul className="description__list">
                <li>Salo de beleza</li>
                <li>Barbearia</li>
                <li>Estdio de tatuagem</li>
                <li>Clinica Esttica</li>
              </ul>
            </div>
          </div>
          <div className="about__column">
            <iframe
              className="about__video"
              src="https://www.youtube.com/embed/J1mnZchxJQ4"
              width="100%"
              height="100%"
              frameborder="0"
              webkitallowfullscreen
              mozallowfullscreen
              allowfullscreen
            />
          </div>
        </div>
      </section>
    );
  }
}

export default Video;



